import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"
Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState({
    reducer(val) {
      return {
        notes: val.notes,
        cardsort: val.cardsort,
        timemaxsort: val.timemaxsort,
        count: val.count,
        cursort: val.cursort,
        sorts: val.sorts,
      }
    }
  })],
  state: {
    notes: [],
    curnotes: {},
    cardcolor: "",
    count: 1,
    ExitActive: true,
    EditsortActive: true,
    cardsort: false,
    timemaxsort: true,
    cursort: { name: "預設筆記本", id: 2 },
    sorts: [{ name: "所有筆記本", id: 1 }, { name: "預設筆記本", id: 2 }, { name: "test", id: 3 }]
  },
  mutations: {
    AddNote(state) {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1 >= 10 ? date.getMonth() + 1 : "0" + (date.getMonth() + 1);
      let day = date.getDate() >= 10 ? date.getDate() : "0" + date.getDate();
      let hour = date.getHours();
      let min = date.getMinutes() >= 10 ? date.getMinutes() : "0" + date.getMinutes();
      let sec = date.getSeconds() >= 10 ? date.getSeconds() : "0" + date.getSeconds();
      let curdate = year + "-" + month + "-" + day + " " + hour + ":" + min + ":" + sec;
      let note = {
        datetime: curdate,
        content: "",
        sort: state.cursort.id == 1 ? 2 : state.cursort.id,
        id: ++state.count,
      };
      state.notes.push(note);
      state.curnotes = note;

    },
    DeleteNote(state, id) {
      let deleteindex = 0;
      state.notes.forEach((e, index) => {
        if (e.id == id)
          deleteindex = index;
      });
      state.notes.splice(deleteindex, 1);

    },
    UpdateNote() { },
    EditNote(state, id) {

      state.notes.forEach(e => {
        if (e.id === id) {
          e.content = state.curnotes.content;
        }
      });

    },
    ChangeSort(state, text) {
      state.cursort = text;
    },
    AddSort(state, text) {
      let count = state.sorts[state.sorts.length - 1].id + 1;
      state.sorts.push({ name: text, id: count });

    },
    DeleteSort(state, text) {
      state.sorts.push(text);

    },
    ChangeSortname(state, sort) {
      state.sorts.forEach(e => {
        if (e.id == sort.id) {
          e.name = sort.name;
        }
      })
    },
    changecardsort(state, args) {
      state.cardsort = args;
    },
    timemaxsort(state, args) {
      state.timemaxsort = args;
    }

  },
  actions: {
    addnote({ commit }) {
      commit("AddNote");
    },
    editnote({ commit }, id) {
      commit("EditNote", id);
    },
    deletenote({ commit }, id) {
      commit("DeleteNote", id);
    },
    changesort({ commit }, text) {
      commit("ChangeSort", text);
    },
    addsort({ commit }, text) {
      commit("AddSort", text);
    }
  },
  getters: {
    notescard: state => state.notes.filter(e => {
      if (state.cursort.id == 1) {
        return true;
      }
      else {
        if (e.sort == state.cursort.id)
          return true;
      }

    }),
    notes: state => state.notes,
    curnotes: state => state.curnotes,
    exitactive: state => state.ExitActive,
    editsortactive: state => state.EditsortActive,
    sorts: state => state.sorts,
    cursort: state => state.cursort,
    cardsort: state => state.cardsort,
    timemaxsort: state => state.timemaxsort,
    sortnum: state => {
      let obj = { 1: 0 };
      let self = state;
      state.notes.forEach(e => {

        self.sorts.forEach(ex => {
          if (e.sort == ex.id) {
            if (obj[ex.id]) {
              obj[ex.id]++;
            }
            else {
              obj[ex.id] = 1;
            }

          } else {
            if (!obj[ex.id]) {
              obj[ex.id] = 0;
            }
          }

        });
        obj[1]++;
      });
      console.log(obj);
      return obj;
    },
  },
  modules: {
  }
})
