<template>
  <div class="wrap" :class="{ exit: exitactive }">
    <div class="container dateblack">
      <div class="header gray">
        <i class="fas fa-arrow-left" @click="returnpage"></i>
        <span class="select" @click="sortselect">{{ sortname }}</span>
        <i
          class="fas fa-chevron-down"
          :class="{ chevronrate: chevronactive }"
        ></i>
        <i class="fas fa-trash-alt" @click="deletecard"></i>
      </div>
      <div class="content">
        <div class="date">{{ curnotes.datetime }}</div>
        <textarea
          name=""
          :value="curnotes.content ? curnotes.content : ''"
          @change="textchange"
        ></textarea>
        <ul class="sortcontent" :class="{ enter: sortenteractive }">
          <li
            v-for="(item, index) in sorts"
            :key="index"
            v-show="item.id != 1"
            @click="changecurnotes(item.id)"
          >
            <span>{{ item.name }}</span
            ><i class="fas fa-check" v-if="item.id == curnotes.sort"></i>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      sortenteractive: false,
      chevronactive: false,
    };
  },
  methods: {
    returnpage() {
      this.sortenteractive = false;
      this.chevronactive = false;
      this.$store.state.ExitActive = true;
    },
    sortselect() {
      this.sortenteractive = !this.sortenteractive;
      this.chevronactive = !this.chevronactive;
    },
    textchange(e) {
      this.$store.state.curnotes.content = e.target.value;
      this.$store.dispatch("editnote", this.curnotes.id);
    },
    deletecard() {
      this.$store.dispatch("deletenote", this.curnotes.id);
      this.returnpage();
    },
    changecurnotes(item) {
      this.$store.state.curnotes.sort = item;

      this.sortselect();
    },
  },
  computed: {
    exitactive() {
      return this.$store.getters.exitactive;
    },
    sorts() {
      return this.$store.getters.sorts;
    },
    cursort() {
      return this.$store.getters.cursort;
    },
    curnotes() {
      return this.$store.getters.curnotes;
    },
    sortname() {
      if (Object.keys(this.curnotes).length != 0) {
        let obj = this.sorts.filter((e) => {
          if (e.id === this.curnotes.sort) {
            return true;
          }
        });
        return obj[0].name;
      }
      return "";
    },
  },
};
</script>
<style scoped>
.wrap {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background-color: white;
  padding: 40px;
  box-sizing: border-box;
  top: 0;
  transition: top 0.5s;
}

.select {
  cursor: pointer;
  user-select: none;
}
.chevronrate {
  transform: rotate(180deg);
}
.sortcontent {
  position: absolute;
  background-color: white;
  width: 100%;
  height: calc(100% - 60px);
  top: -100%;
  left: 0;
  padding: 10px 20px;
  box-sizing: border-box;
  transition: top 0.2s;
}

.enter {
  top: 0;
}
.sortcontent li {
  color: black;
  text-align: left;
  padding: 10px 0;
  display: flex;
}
.fa-check {
  margin-left: auto;
  color: red;
}
.exit {
  top: 100%;
}
.container {
  height: 90%;
}
.header {
  display: flex;
  height: 40px;
  font-size: 30px;
  align-items: center;
  padding: 10px 30px 10px 10px;
}
.fa-arrow-left {
  margin: 0 60px 0 0;
  cursor: pointer;
}
.fa-chevron-down {
  margin-left: 20px;
  transition: 0.5s;
}
.fa-trash-alt {
  margin-left: auto;
  color: tomato;
  cursor: pointer;
}
.content {
  text-align: center;
  padding: 0 20px;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.date {
  font-size: 30px;
  padding: 10px 0;
}
textarea {
  width: 100%;
  height: calc(100% - 134px);
  resize: none;
  background-color: transparent;
  color: white;
  font-size: 30px;
  border: none;
}
</style>