<template>
  <div
    class="wrap dateblack"
    @click="editcard"
    :class="{ wrapwidth: cardsort }"
  >
    <h1 class="date gray">{{ date }}</h1>
    <textarea class="content" :value="content" readonly="readonly"> </textarea>
  </div>
</template>
<script>
export default {
  name: "notescard",
  props: ["date", "content", "sort", "id", "item"],
  data() {
    return {};
  },
  methods: {
    editcard() {
      this.$store.state.ExitActive = false;
      this.$store.state.curnotes = this.item;
    },
  },
  computed: {
    cardsort() {
      return this.$store.getters.cardsort;
    },
  },
};
</script>
<style scoped>
.wrap {
  width: calc(50% - 60px);
  text-align: center;
  margin: 10px 30px;
  height: 400px;
  box-sizing: border-box;
  overflow: hidden;
}
.wrapwidth {
  width: 100%;
  height: 100px;
}
.content {
  width: 100%;
  word-break: break-all;
  font-size: 25px;
  text-align: left;
  resize: none;
  overflow: hidden;
  background: transparent;
  outline: none;
  color: white;
  border: none;
  height: calc(100% - 36px);
}
</style>