<template>
  <div id="app">
    <div class="narbar" :class="{ w400: navbaractive }">
      <div class="narbar_content w400">
        <div class="title">嚕嚕米筆記</div>
        <ul class="notes">
          <li
            v-for="item of sorts"
            :key="item.id"
            :class="{ notesactive: item.id == cursort.id }"
            @click="changesort(item)"
          >
            {{ item.name }}
          </li>
          <li class="editnotes" @click="editsortenter">編輯筆記本</li>
        </ul>
        <div class="toolwrap">
          <i
            class="fab fa-microsoft fareactive"
            @click="sortchagnewidth(false)"
            :class="{ actviewidth: !cardsort }"
          ></i>
          <i
            class="fas fa-align-justify fareactive"
            @click="sortchagnewidth(true)"
            :class="{ actviewidth: cardsort }"
          ></i>
          <i class="fas fa-cog"></i>
          <i
            class="fas fa-sort-amount-down fareactive"
            @click="sortchagnemax(true)"
            :class="{ actviewidth: timemaxsort }"
          >
          </i>
          <i
            class="fas fa-sort-amount-up fareactive"
            @click="sortchagnemax(false)"
            :class="{ actviewidth: !timemaxsort }"
          ></i>
          <i class="fas fa-heart"></i>
        </div>
      </div>
    </div>
    <div class="main" :class="{ m400: mainactive }">
      <div
        class="backreturn"
        @click="backgroundreturn"
        v-show="this.navbaractive"
      ></div>
      <div class="toolbar">
        <div class="pic">
          <img src="./assets/moom.png" alt="" @click="navbarbt" />
        </div>
        <div class="addbt">
          <i class="fas fa-plus-square" @click="addnote"></i>
        </div>
      </div>
      <div class="main_content">
        <notescard
          v-for="item of notecard"
          :key="item.id"
          :content="item.content"
          :date="item.datetime"
          :id="item.id"
          :sort="item.sort"
          :item="item"
        />
        <editcard />
        <editsort />
      </div>
    </div>
  </div>
</template>
<script>
import notescard from "@/components/notescard.vue";
import editcard from "@/components/editcard.vue";
import editsort from "@/components/editsort.vue";
export default {
  components: {
    notescard,
    editcard,
    editsort,
  },
  data() {
    return {
      navbaractive: false,
      mainactive: false,
      navrclick: false,
    };
  },
  computed: {
    notecard() {
      let sorts = this.$store.getters.notescard;
      sorts.sort((a, b) => {
        if (this.timemaxsort == true) {
          return (
            Date.parse(b.datetime).valueOf() - Date.parse(a.datetime).valueOf()
          );
        } else {
          return (
            Date.parse(a.datetime).valueOf() - Date.parse(b.datetime).valueOf()
          );
        }
      });
      return sorts;
    },
    sorts() {
      return this.$store.getters.sorts;
    },
    cursort() {
      return this.$store.getters.cursort;
    },
    editsortactive() {
      return this.$store.getters.editsortactive;
    },
    cardsort() {
      return this.$store.getters.cardsort;
    },
    timemaxsort() {
      return this.$store.getters.timemaxsort;
    },
  },
  methods: {
    navbarbt() {
      this.navbaractive = !this.navbaractive;
      this.mainactive = !this.mainactive;
      this.navrclick = true;
    },
    addnote() {
      this.$store.dispatch("addnote");
    },
    backgroundreturn() {
      this.navbaractive = false;
      this.mainactive = false;
    },
    changesort(item) {
      this.backgroundreturn();
      this.$store.dispatch("changesort", item);
    },
    editsortenter() {
      this.backgroundreturn();
      this.$store.state.EditsortActive = false;
    },
    sortchagnewidth(item) {
      this.$store.commit("changecardsort", item);
      this.backgroundreturn();
    },
    sortchagnemax(item) {
      this.$store.commit("timemaxsort", item);
      this.backgroundreturn();
    },
  },
};
</script>
