<template>
  <div class="wrap" :class="{ wrapexit: editsortactive }">
    <div class="editsort" :class="{ editsortenter: addsortactive }">
      <input type="text" v-model="text" @keyup.enter="addsort" />
      <div class="editbtwrap">
        <i class="far fa-trash-alt" v-show="sortedit"></i>
        <i class="fas fa-check" @click="addsort"></i>
      </div>
    </div>
    <div class="container">
      <div class="header">
        <i class="fas fa-arrow-left" @click="returnmain"></i>
        <i class="fas fa-plus" @click="addsortenter"></i>
      </div>
      <div class="content" @click="addsortexit">
        <ul>
          <li
            v-for="item in sorts"
            :key="item.id"
            @click="changesortname(item.name, item.id)"
            v-show="item.id != 1"
          >
            <span>{{ item.name }}</span>
            <span class="sortunm">{{ sortnum[item.id] }}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      addsortactive: false,
      text: "",
      sortclick: false,
      sortedit: false,
      id: 1,
    };
  },
  methods: {
    addsortenter() {
      this.addsortactive = true;
    },
    addsortexit() {
      if (this.sortclick == true) {
        this.sortclick = false;
        return;
      }
      this.text = "";
      this.sortedit = false;
      this.addsortactive = false;
    },
    addsort() {
      if (this.sortedit == false) {
        if (this.text != "") {
          this.$store.dispatch("addsort", this.text);
          this.addsortexit();
          this.text = "";
        }
      } else {
        this.$store.commit("ChangeSortname", { name: this.text, id: this.id });
        this.addsortexit();
        this.text = "";
      }
    },
    changesortname(name, id) {
      this.sortclick = true;
      this.text = name;
      this.id = id;
      this.sortedit = true;
      this.addsortenter();
    },
    returnmain() {
      this.$store.state.EditsortActive = true;
    },
  },
  computed: {
    sorts() {
      return this.$store.getters.sorts;
    },
    sortnum() {
      return this.$store.getters.sortnum;
    },
    editsortactive() {
      return this.$store.getters.editsortactive;
    },
  },
};
</script>
<style scoped>
.wrap {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: black;
  top: 0;
  left: 0;
  transition: 0.5s;
}
.wrapexit {
  left: -150%;
}
.header {
  padding: 20px 20px;
  display: flex;
}
.fa-plus {
  margin-left: auto;
}
.fas {
  color: white;
  font-size: 30px;
  cursor: pointer;
}
.fa-check {
  color: blueviolet;
  align-self: flex-end;
  cursor: pointer;
  margin-left: auto;
}
.editsort {
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  padding: 20px 20px;
  box-sizing: border-box;
  background-color: white;
  top: -100%;
  transition: 0.5s;
}
.editbtwrap {
  display: flex;
  margin-top: 10px;
  justify-content: space-between;
  align-items: center;
}
.fa-trash-alt {
  font-size: 30px;
  color: red;
}
.editsortenter {
  top: 0;
}
.editsort input {
  padding: 10px;
}
.container {
  height: 100%;
}
.content {
  height: 100%;
  padding: 0 30px;
  box-sizing: border-box;
}
.content ul li {
  font-size: 30px;
  color: white;
  padding: 10px 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.sortunm {
  margin-left: auto;
}
.content ul li:hover {
  color: tomato;
}
</style>